import errorCode from "@/utils/errorCode";
import {
	config
} from '@/config.js'
import { getToken } from '@/api/user'
import { getCurrentPagePath } from '@/utils/xmcet'
import store from '@/store'

// request
export const apiResquest = (params) => {
  var url = config.base_url + params.url;
  // GET
  if (!params.method) {
    params.method = "GET";
  }
  return new Promise((resolve, reject) => {
    uni.showLoading({
        title: '',
        mask: true
    })
    return uni.request({
      url: url,
      data: params.data,
      header: {
        "content-type": "application/json",
        token: wx.getStorageSync("token"), // token
      },
      method: params.method,
      success: function (res) {
        uni.hideLoading()
        // 
        const code = res.data.code || 200;
        // 
        const message = errorCode[code] || res.data.msg || errorCode["default"];
        // token
        if (code === 401) {
          // 

            uni.login({
              provider: 'weixin',
              success: (loginRes) => {
                getToken({code: loginRes.code}).then(response => {
                  console.log(response)
                  // token,
                  // wx.setStorageSync('authorized', response.data.authorized);
                  wx.setStorageSync('token', response.data.token);
                  store.commit('SET_AUTHORIZED', response.data.authorized)
                  store.commit('SET_TOKEN', response.data.token)
                  console.log(encodeURIComponent(getCurrentPagePath()))
                  uni.redirectTo({
                    url: '/pages/refresh/refresh?from=' + encodeURIComponent(getCurrentPagePath())
                  })
                })
              }
            });

        } else if (code === 500) {
          wx.showToast({
              title: "o()o",
              icon: "none",
          });
          return;
        } else if (code !== 200) {
          wx.showToast({
              title: message,
              icon: "none",
          });
          return;
        }
        // 
        resolve(res.data);
      },
      fail: (err) => {
				reject(err);
				uni.hideLoading()
			},
			complete: () => {
				uni.hideLoading()
			}
    });
  })
  
}
